import React, { useRef } from 'react'
import styled from 'styled-components'
import { useOnClickOutside } from '../../hooks/useOnClickOutside'
import { ApplicationModal } from '../../state/application/actions'
import { useModalOpen, useToggleModal } from '../../state/application/hooks'
import i18next from '../../i18n'

// import { ExternalLink } from '../../theme'
// import { useTranslation } from 'react-i18next'
// import { ReactComponent as MenuIcon } from '../../assets/images/icon_language.svg'
import IconLanguage from '../../assets/images/IconLanguage.png'

// const StyledMenuIcon = styled(MenuIcon)`
//   path {
//     stroke: ${({ theme }) => theme.text1};
//   }
// `

const StyledLanguageButton = styled.div`
  width: 100%;
  height: 100%;
  border: none;
  display: flex;
  align-items: center;
  background-color: transparent;
  margin: 0;
  padding: 0;
  height: 36px;

  :hover,
  :focus {
    cursor: pointer;
    outline: none;
  }

  svg {
    margin-top: 2px;
  }
`

const StyledLanguage = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: none;
  text-align: left;
`

const LanguageFlyout = styled.span`
  min-width: 8.125rem;
  background-color: #fff;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.01), 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 16px 24px rgba(0, 0, 0, 0.04),
    0px 24px 32px rgba(0, 0, 0, 0.01);
  border-radius: 12px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  position: absolute;
  top: 3rem;
  right: 0rem;
  z-index: 100;

`

const LanguageItem = styled.div`
  flex: 1;
  padding: 0.5rem 0.5rem;
  color: ${({ theme }) => theme.text2};
  :hover {
    color: ${({ theme }) => theme.text1};
    cursor: pointer;
    text-decoration: none;
  }
  > svg {
    margin-right: 8px;
  }
  &.active {
    color: ${({ theme }) => theme.primary1}
  }
`

// const CODE_LINK = 'https://github.com/Uniswap/uniswap-interface'

export default function Language() {
  // const { account } = useActiveWeb3React()

  const node = useRef<HTMLDivElement>()
  const open = useModalOpen(ApplicationModal.LANGUAGE)
  const toggle = useToggleModal(ApplicationModal.LANGUAGE)
  useOnClickOutside(node, open ? toggle : undefined)
  // const { t } = useTranslation()
  // const openClaimModal = useToggleModal(ApplicationModal.ADDRESS_CLAIM)

  return (
    // https://github.com/DefinitelyTyped/DefinitelyTyped/issues/30451
    <StyledLanguage ref={node as any}>
      <StyledLanguageButton onClick={toggle}>
        {/* <StyledMenuIcon /> */}
        <img width={'36px'} src={IconLanguage} alt="" />
      </StyledLanguageButton>

      {open && (
        <LanguageFlyout>
          {/* <LanguageItem className={i18next.language === 'cn' ? 'active' : ''} onClick={() => i18next.changeLanguage('cn')}>
            中文
          </LanguageItem> */}
          <LanguageItem className={i18next.language === 'tw' ? 'active' : ''} onClick={() => {
            i18next.changeLanguage('tw');
            toggle()
          }}>
            繁體
          </LanguageItem>
          <LanguageItem className={i18next.language === 'en' ? 'active' : ''} onClick={() => {
            i18next.changeLanguage('en');
            toggle()
          }}>
            English
          </LanguageItem>
          {/* <LanguageItem className={i18next.language === 'ko' ? 'active' : ''} onClick={() => i18next.changeLanguage('ko')}>
            한국어
          </LanguageItem> */}
        </LanguageFlyout>
      )}
    </StyledLanguage>
  )
}
